.info-list {
    .page-title {
        margin: 8px 0 58px;
    }

    &__wrap {
        padding-bottom: 59px;
    }

    &__item {
        display: flex;

        &:not(:last-of-type) {
            border-bottom: 1px solid #d5d5d5;
            padding-bottom: 40px;
            margin-bottom: 39px;
        }

        .image {
            height: 240px;
            width: 400px;
            border-radius: 6px;
            overflow: hidden;
            
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .content {
            width: calc(100% - 400px);
            padding-left: 50px;
            padding-top: 12px;

            .name {
                font-size: 25px;
                font-weight: 600;
            }

            p {
                font-size: 17px;
                font-weight: 400;
                line-height: 1.7;
                margin: 22px 0 26px;
            }

            a:not(.name) {
                .btn-orange;
                .fullrounded;
                padding: 8px 19px;
                display: inline-block;
            }
        }
    }
}

.info-detail {
    .page-title {
        line-height: 1.4;
        margin: 3px 0 20px;
    }

    .wysiwyg {
        margin-bottom: 63px;

        p {
            line-height: 2.1;
            margin: 20px 0 33px;
        }
    
        p,
        ul,
        ol, 
        table {
            font-weight: 300;
        }
    
    }

    &__btn {
        text-align: center;
        padding: 38px 0 58px;
        border-top: 1px solid #e9eae9;

        a {
            .btn-blue;
            .fullrounded;
            padding: 10px 18px;
            display: inline-block;
        }
    }
}

@media screen and (max-width: 1200px) {
    .info-list {
        &__item {
            .image {
                width: 300px;
                height: 200px;
            }
            
            .content { 
                width: calc(100% - 300px);

                .name {
                    font-size: 20px;
                }

                p {
                    font-size: 15px;
                }
            }
        }
    }
}

@media screen and (max-width: 992px) {
    .info-list {
        &__item {
            .image {
                width: 200px;
                height: 170px;
            }
            
            .content { 
                width: calc(100% - 200px);
                padding-left: 20px;

                .name {
                    font-size: 17px;
                }

                p {
                    font-size: 12px;
                }

                a:not(.name) {
                    font-size: 12px;
                }
            }
        }
    }
}

@media screen and (max-width: 576px) {
    .info-list {
        &__item {
            display: block;

            .image {
                display: block;
                width: 100%;
            }

            .content {
                padding-left: 0;
                width: 100%;
            }
        }
    }
}